import React from "react";

const SideMenu = ({ isMenuClosed }) => {
  return (
    <>
      {" "}
      <div className={`sidebar ${isMenuClosed ? "closed" : ""}`}>
        <div className="sideLogo">
          <div className="logo" onClick={() => (window.location.href = "/")}>
            <img
              src={require("./Screenshot_2025-10-02_122721-removebg-preview.png")}
              alt=""
            />
            <img
              src={require("./Screenshot_2025-10-02_122819-removebg-preview.png")}
              alt=""
              className="logo-text"
            />
          </div>
          <p>
            لوريم إيبسوم هو ببساطة نص شكلي (بمعنى أن الغاية هي الشكل وليس
            المضمون) ويستخدم في صناعات المطابع ودور النشر. كان لوريم إيبسوم
          </p>
        </div>
        <div className="mini-categories"></div>
        <div className="information">
          <h1>
            معلومات التواصل معنا <i className="fas fa-info-circle"></i>
          </h1>
          <div className="whatsApp info-div">
            <div className="line">
              <i className="fas fa-phone-volume fa-fw"></i>
              <div className="info">
                <span>+963936093445</span>
              </div>
            </div>
            <div className="line">
              <i className="fas fa-envelope"></i>
              <div className="info">
                <span>rawatemmo@gmail.com</span>
              </div>
            </div>
            <div className="line">
              <i className="fas fa-map-marker-alt fa-fw"></i>
              <div className="info">شارع الحسكة دوار السياحي</div>
            </div>
            <div className="line">
              <i className="far fa-clock fa-fw"></i>
              <div className="info">من الساعة 10:00 إلى 18:00</div>
            </div>
          </div>
        </div>
        <div className="socialLinks">
          <div className="social-icons">
            <div className="social-menu">
              <ul className="">
                <li>
                  <a href="https://github.com/sanketbodke" target="blank">
                    <i className="fab fa-whatsapp"></i>
                  </a>
                </li>
                <li>
                  <a
                    href="https://www.instagram.com/imsanketbodke/"
                    target="blank"
                  >
                    <i className="fab fa-instagram"></i>
                  </a>
                </li>
                <li>
                  <a
                    href="https://www.linkedin.com/in/sanket-bodake-995b5b205/"
                    target="blank"
                  >
                    <i className="fab fa-facebook-f"></i>
                  </a>
                </li>
              </ul>
            </div>
          </div>
        </div>
        <div className="rights">
          <p>designed by @Core_SoftWares</p>
        </div>
      </div>
    </>
  );
};

export default SideMenu;
