const Card = ({ id, title, unit, image }) => {
  return (
    <div className="card" aria-label={title}>
      <div className="card-background">
        <div className="item-thumb">
          <img
            src={image || require("./item.png")}
            alt={title}
            style={{ objectFit: "cover" }}
          />
        </div>
        <div className="item-content">
          <h3>{title}</h3>
          <h6>{unit.toFixed(2)}</h6>
        </div>
      </div>
    </div>
  );
};

export default Card;
