import { useParams } from "react-router";
import { useQuery } from "@tanstack/react-query";
import APIClient from "../../utils/ApiClient"; // your API client
import Card from "./Card"; // the same card component for items
import { imageUrl } from "../../utils/axios";

const apiClient = new APIClient("items");

const Categories = () => {
  const { id } = useParams(); // type ID

  const { data, isLoading, isError } = useQuery({
    queryKey: ["itemsByType", id],
    queryFn: () => apiClient.getAll({ filters: { typeId: id } }),
    keepPreviousData: true,
  });

  if (isLoading) return <p>Loading items...</p>;
  if (isError) return <p>Something went wrong.</p>;

  const items = data?.data || [];

  return (
    <div className="container flex">
      {items.length === 0 && <p>No items found in this category.</p>}
      {items.map((item) => (
        <Card
          key={item._id}
          id={item._id}
          title={item.name}
          unit={parseFloat(item.price)} // assuming price is a string in DB
          image={item.image ? `${imageUrl}${item.image}` : null}
          rating={5} // keep rating as static if you don't have it in DB
        />
      ))}
    </div>
  );
};

export default Categories;
