import React, { useEffect, useState } from "react";
import "./navbar.css";
import "./ul.css";
import { Link, Outlet } from "react-router";
import SideMenu from "./SideMenu";
const Navbar = () => {
  const [isMenuClosed, setIsMenuClosed] = useState(true);

  const closeMenu = () => {
    setIsMenuClosed(!isMenuClosed);
  };
  // close on click outside
  const handleClickOutside = (event) => {
    if (!event.target.closest(".menu")) {
      setIsMenuClosed(true);
    }
  };
  useEffect(() => {
    window.addEventListener("click", handleClickOutside);
    return () => {
      window.removeEventListener("click", handleClickOutside);
    };
  });

  return (
    <>
      <nav className="flex header">
        <div className="logo" onClick={() => (window.location.href = "/")}>
          <img
            src={require("./Screenshot_2025-10-02_122721-removebg-preview.png")}
            alt=""
          />
          <img
            src={require("./Screenshot_2025-10-02_122819-removebg-preview.png")}
            alt=""
            className="logo-text"
          />
        </div>
        <div className="menu">
          <i onClick={closeMenu} className="fa-solid fa-bars-staggered"></i>
          <SideMenu isMenuClosed={isMenuClosed} />
        </div>
      </nav>
      <Outlet />
    </>
  );
};

export default Navbar;
