import { NavLink } from "react-router";
import "./dashboard.css";
import { links } from "./links";
const DashboardSideBar = () => {
  return (
    <aside className="dashboard-sidebar">
      {links?.map((e) => (
        <NavLink key={e.to} to={e.to}>
          {e.icon}
          <span>{e.title}</span>
        </NavLink>
      ))}
    </aside>
  );
};

export default DashboardSideBar;
