import { lazy } from "react";
import PageFallback from "../../../../components/PageFallback";
const ItemTable = lazy(() => import("./Item"));
export const itemRouter = [
  {
    path: "",
    element: (
      <PageFallback>
        <ItemTable />
      </PageFallback>
    ),
  },
  {
    path: "items",
    element: (
      <PageFallback>
        <ItemTable />
      </PageFallback>
    ),
  },
];
