import React, { use, useEffect } from "react";
import { Link } from "react-router";

const DashBoardNav = () => {
  const closeMenu = () => {
    const sidebar = document.querySelector(".dashboard-sidebar");
    sidebar.classList.toggle("dashboard-sidebar-opened");
  };
  const handleClickOutside = (e) => {
    if (
      !e.target.closest(".dashboard-sidebar") &&
      !e.target.closest(".fa-bars-staggered")
    ) {
      const sidebar = document.querySelector(".dashboard-sidebar");
      sidebar.classList.remove("dashboard-sidebar-opened");
    }
  };
  useEffect(() => {
    window.addEventListener("click", handleClickOutside);
    return () => {
      window.removeEventListener("click", handleClickOutside);
    };
  }, []);

  return (
    <>
      <nav className="flex header">
        <div className="logo" onClick={() => (window.location.href = "/")}>
          <img
            src={require("./Screenshot_2025-10-02_122721-removebg-preview.png")}
            alt=""
          />
          <img
            src={require("./Screenshot_2025-10-02_122819-removebg-preview.png")}
            alt=""
            className="logo-text"
          />
        </div>
        <div className="menu">
          <i onClick={closeMenu} className="fa-solid fa-bars-staggered"></i>
        </div>
      </nav>
    </>
  );
};

export default DashBoardNav;
